import CoordinatorRequirementsAnalyzer from './CoordinatorRequirementsAnalyzer.js';

export default class LocalFragmentManager{

	constructor(modeler, url) {
		this.modeler=modeler;
		this.url=url;
		this.coordChanges=[];

		this.coordinatorRequirementsAnalyzer=new CoordinatorRequirementsAnalyzer(this.modeler);

		this.saveAsDirty=this.saveAsDirty.bind(this);
		this.saveLocalFragment=this.saveLocalFragment.bind(this);
		this.saveOnlyLocalFragment=this.saveOnlyLocalFragment.bind(this);
		this.synchronizeFragmentWithBigPicture=this.synchronizeFragmentWithBigPicture.bind(this);
		this.saveDirtyLocalChanges=this.saveDirtyLocalChanges.bind(this);
	}
	

	saveAsDirty(highlightModifications, showModifications){
		
		var _this=this;
		
		this.modeler.saveXML({ format: true }, function(err, xml) {
				if (err) {
					showMessage("Error","Could not save BPMN 2.0 diagram");
					return console.error('Could not save BPMN 2.0 diagram', err);
				}
			 	var newFragmentXML=xml.replace(/"/g, "'");

			 	var changes=_this.coordinatorRequirementsAnalyzer.getChanges(highlightModifications);
			 	var id=sessionStorage.getItem("composition").replaceAll(" ", "")+"_"+sessionStorage.getItem("microservicename").replaceAll(" ", "")+"_fragment";

				const definitions=_this.modeler.get('canvas').getRootElement().businessObject.$parent;
				_this.modeler.saveXML({ format: true }, function(err, xml) {	
					var dirtyFragmentXML=xml.replace(/"/g, "'");
					var datos=JSON.stringify({
							"fragment":id,
							"composition":sessionStorage.getItem("composition"),
							"xml":newFragmentXML,
							"dirtyXml":dirtyFragmentXML,
							"description":JSON.stringify(changes),
							"microservice":sessionStorage.getItem("microservicename")
					});

					_this.saveDirtyLocalChanges(datos, changes, showModifications);

				});
				

		 });
	}

	
	saveDirtyLocalChanges(changedFragment, changes, showModifications){
		//var _this=this; 

		$("#sending-loader").css("display","inline");
    	fetch(this.url+"/local/change",{
	 		method:'POST',
	 		headers:{
				"Content-Type":"application/json",
			},
	 		body: changedFragment
		})
	    .then(function (response) {
	        return response.json()
	    })
	    .then(result => {
	    	if(result.responses==-1 && result.participants==-1){ //Automatic adaptation
	    		this.modeler.importXML(JSON.parse(changedFragment).xml, (err, warnings) => {
		           showMessage("Automatic Adaptation","Composition updated locally and synchronized with the global composition and the affected participants");
		        });
	    	}else{
	        	showModifications(changes, result.responses, result.participants);
	        }
	        $("#sending-loader").css("display","none");
	        //TO CHECK --> _this.modeler.get('commandStack').length=0;
	    })
	    .catch(error => {
			console.log(error);
			showMessage("Error","Some error occurs when updating the local BPMN Fragment");
	    })
	}


	synchronizeFragmentWithBigPicture(datos){
		//var _this=this;

		fetch(this.url+"/fragmentmanagerurl")
	    .then(function (response) {
	        return response.text()
	    })
	    .then(fragmentManagerURL => {
	        	fetch(fragmentManagerURL+"/fragments",{
			 		method:'POST',
			 		headers:{
						"Content-Type":"application/json",
					},
			 		body: datos
				})
			    .then(function (response) {
			        return response.text()
			    })
			    .then(result => {
			    		$("#sending-loader").css("display","none");
			        	if(result=="1"){
							showMessage("Message","Composition updated locally and synchronized with the global composition");
							//TO CHECK --> _this.modeler.get('commandStack').length=0;
						}else{
							showMessage("Attention","Some error has happened updating the global composition");
						}
			    })
			    .catch(error => {
			        	$("#sending-loader").css("display","none");
						console.log(error);
						showMessage("Error","Some error occurs when synchronizing with the global composition");
			    })
		})
	    .catch(error => {
	        	$("#sending-loader").css("display","none");
				console.log(error);
				showMessage("Error","Some error occurs when obtainin Fragment Manager url");
	    })

	}


	saveLocalFragment(){
			$("#sending-loader").css("display","inline");
			
		   	const definitions=this.modeler.get('canvas').getRootElement().businessObject.$parent;

		   	var url=this.url;
		   	var id=sessionStorage.getItem("composition").replaceAll(" ", "")+"_"+sessionStorage.getItem("microservicename").replaceAll(" ", "")+"_fragment";

		   	const synchronizeFragmentWithBigPicture=this.synchronizeFragmentWithBigPicture;

			this.modeler.saveXML({ format: true }, function(err, xml) {
				if (err) {
					showMessage("Error","Could not save BPMN 2.0 diagram");
					return console.error('Could not save BPMN 2.0 diagram', err);
				}
			 	var datos=JSON.stringify({
					"id":id,
					"microservice":sessionStorage.getItem("microservicename"),
					"composition":sessionStorage.getItem("composition"),
					"xml":xml.replace(/"/g, "'")
				});
			
				fetch(url+"/fragments", {
				 		method:'POST',
				 		headers:{
							"Content-Type":"application/json",
						}, 
				 		body: datos
				})
			    .then(function (response) {
			        return response.text()
			    })
			    .then(result => {
			        	if(result=="1"){
							console.log("Composition updated locally");
							synchronizeFragmentWithBigPicture(datos);
						}else{
							showMessage("Attention","Error when updating the local fragment");
						}
			    }) 
			    .catch(error => {
			        	$("#sending-loader").css("display","none");
						console.log(error);
						showMessage("Error","Some error occurs when updating the local BPMN Fragment");
			    })
		    });
	}

	saveOnlyLocalFragment(){
			$("#sending-loader").css("display","inline");
			
		   	const definitions=this.modeler.get('canvas').getRootElement().businessObject.$parent;
		
		   	var url=this.url;
		   	var id=sessionStorage.getItem("composition").replaceAll(" ", "")+"_"+sessionStorage.getItem("microservicename").replaceAll(" ", "")+"_fragment";

			this.modeler.saveXML({ format: true }, function(err, xml) {
				if (err) {
					showMessage("Error","Could not save BPMN 2.0 diagram");
					return console.error('Could not save BPMN 2.0 diagram', err);
				}
		
			 	var datos=JSON.stringify({
					"id":id,
					"microservice":sessionStorage.getItem("microservicename"),
					"composition":sessionStorage.getItem("composition"),
					"xml":xml.replace(/"/g, "'")
				});
			
				fetch(url+"/fragments", {
				 		method:'POST',
				 		headers:{
							"Content-Type":"application/json",
						}, 
				 		body: datos
				})
			    .then(function (response) {
			        return response.text()
			    })
			    .then(result => {
			        	if(result=="1"){
							showMessage("Attention","BPMN Fragment updated");
						}else{
							showMessage("Attention","Error when updating the local fragment");
						}
						$("#sending-loader").css("display","none");
			    }) 
			    .catch(error => {
			        	$("#sending-loader").css("display","none");
						console.log(error);
						showMessage("Error","Some error occurs when updating the local BPMN Fragment");
			    })
		    });
	}

	checkBPMN(){
		const definitions=this.modeler.get('canvas').getRootElement().businessObject.$parent;

		var poolMicroservice=false;
		var poolEvent=false;
		jQuery.each(definitions.rootElements, function(index, element){
				if (element.$type=="bpmn:Collaboration"){
					jQuery.each(element.participants, function(index, participant){
						if(participant.name.toLowerCase().trim()=="event bus"){
							poolEvent=true;
						}
						if(participant.name.toLowerCase()==sessionStorage.getItem("microservicename").toLowerCase()){
							poolMicroservice=true;
						}
					});
				}
		});

		if(!poolMicroservice){
			showMessage("Error","One pool should be associated to the microservice "+sessionStorage.getItem("microservicename"));
			return false;
		}

		if(!poolEvent){
			console.log("There is no EVENT BUS pool");
			showMessage("Error","There is no EVENT BUS pool");
			return false;
		}

		return true;
	}

	checkRequirementsModifications(){
		const commandStack = this.modeler.get('commandStack');

		var id="";
		var coordReqModified=false;

		var coordinationReqIDs=JSON.parse(sessionStorage.getItem("coordinationReq"));
		var coordinationReqType=["bpmn:IntermediateCatchEvent","bpmn:IntermediateThrowEvent"];
		var addedElements=[];

		commandStack._stack.forEach(action => {

			switch(action.command){

				case "shape.delete":

					if(coordinationReqIDs.includes(action.context.shape.id)){
							coordReqModified=true;
					}else{
						var indice = addedElements.indexOf(action.context.shape.id); 
						if(indice>=0) addedElements.splice(indice, 1);
					}
					break;

				case "elements.delete":

					action.context.elements.every(element => {	
						if(coordinationReqIDs.includes(element.businessObject.id)){
							coordReqModified=true;
							return false; // Exit from every loop
						}else{
							var indice = addedElements.indexOf(element.businessObject.id); 
							if(indice>=0) addedElements.splice(indice, 1);
						}
						return true; // Continue the every loop
					})
					break;

				case "element.updateLabel":

					if(coordinationReqIDs.includes(action.context.element.businessObject.id)){
							coordReqModified=true;
					}
					break;

				case "shape.create": 

					if(coordinationReqType.includes(action.context.shape.type)){
							addedElements.push(action.context.shape.id);
					}

			}
		});

		if(coordReqModified==true) return -1; // Event-based elements deleted or updated
		else if(addedElements.length>0 && coordReqModified==false) return 1; //There is added event-based elements
		else return 0; //There is no changes

	}

}