import ReactDOM from 'react-dom';
import React, { Component } from 'react';
import Dialog from './Dialog.js';
import {showParticipantChanges} from './ParticipantChanges.js';
import {showDirty} from './DirtyDialog.js';


export default class FragmentDialog extends React.Component {

  constructor(props) {
    super(props);
    this.state = {
      fragments: [],
      error:null,
      type:"modal",
      title:"Available fragments",
      id:"fragmentDialog"
    };

    this.loadFragment=this.loadFragment.bind(this);
  }

  componentWillMount() {
    fetch(this.props.url+"/fragments")
      .then(function (response) {
        return response.json()
      })
      .then(frags => {
        this.setState({ fragments: frags })
      })
      .catch(error => {
          this.setState({error:error});
        }
      )    
  }

  loadFragment(composition){
    var _this=this;
  	fetch(this.props.url+"/fragmentbpmn/"+composition)
  	  .then(response => {
        return response.json()
      })
      .then(fragment => {
        this.props.modeler.importXML(fragment.xml, (err, warnings) => {
          const elementRegistry = this.props.modeler.get('elementRegistry');

          var coordinationReq=["bpmn:IntermediateCatchEvent","bpmn:IntermediateThrowEvent"];
          var elements = elementRegistry.filter(function(element) {
                return coordinationReq.includes(element.type) ||
                       ((element.type=="bpmn:StartEvent" || element.type=="bpmn:EndEvent") &&
                        element.businessObject.eventDefinitions!=null &&
                        element.businessObject.eventDefinitions[0].$type=="bpmn:MessageEventDefinition");
          });

          var elementsId=elements.map(element =>{ return element.id});
          sessionStorage.setItem("coordinationReq",JSON.stringify(elementsId));


           //Check if the fragment is a dirty copy
          const participants=this.props.modeler.get('canvas').getRootElement().businessObject.participants;
          participants.forEach(participant => {
            if(participant.name!="EVENT BUS"){
                if(participant.extensionElements && participant.extensionElements.values[0].name=="dirty" && participant.extensionElements.values[0].stringValue=="true"){
                  showDirty(fragment.responses, fragment.participants, fragment.changeId, fragment.accepted);
                }
            }
          });
        
         //Check if other participants have made changes
         if(fragment.changes.length>0){
          showParticipantChanges(fragment.changes);
         }


        });
		    sessionStorage.setItem("composition",composition);
        
		    $("#"+this.state.id).modal("hide");
      });
        
  }

  disableInteraction(){
    this.props.modeler.get("eventBus").on('drag.start', 1500, function(event) {
      event.stopPropagation();
    });
    this.props.modeler.get("eventBus").on('element.dblclick', 1400, function(event) {
      event.stopPropagation();
    });
    this.props.modeler.get("eventBus").on('element.click', 1400, function(event) {
      event.stopPropagation();
    });
  }

  render(){
  		var content;
  		if(this.state.error==null) {
	  		const fragments = this.state.fragments.map(fragment => 
	  			<a key={fragment.COMPOSITION} href="#" className="list-group-item list-group-item-action" onClick={this.loadFragment.bind(this,fragment.COMPOSITION)}>{fragment.COMPOSITION}</a>
	  		);
	  		content=<div className="list-group">{fragments}</div>;
  		}else{
  			content=this.state.error;
  		}
  		return (
  			<Dialog type={this.state.type} title={this.state.title} id={this.state.id} content={content} />
  		);
  }

}