import React, { Component } from 'react';
import LocalFragmentManager from '../local-management/LocalFragmentManager';
import {showDirty} from '../dialogs/DirtyDialog.js';

export default class SendFragmentButton extends Component {

	constructor(props) {
		super(props);

		this.sendFragment = this.sendFragment.bind(this);
		this.saveFragment = this.saveFragment.bind(this);
		this.localFragmentManager=new LocalFragmentManager(this.props.modeler,this.props.url);
	}

	showModifications(modifications, responses, participants){

		var changes="";

		modifications.forEach(modification => { console.log(modification);changes+="<li>"+modification.textualDescription+"</li>";});

		const message= 	"<div>"+
							"<p style='text-align: justify'>Somes changes in the BPMN Fragment affect coordination requirements of the composition.</p>"+
							"<p style='text-align: justify'>Modifications are marked as dirty until an acceptance of other participants is received for the following changes:</p>"+
							"<ul className='list-group'>"+changes+"</ul>"+
						"</div>";
		
		showMessage("Changes need syncronization",message);

		/*$("#localChangeResponses").html(responses);
        $("#compositionParticipants").html(participants);
        $(".dirty").css("display","inline"); */
        showDirty(false, responses, participants);  
	}

	showAddedElements(modifications){

		var changes="";

		modifications.forEach(modification => { console.log(modification);changes+="<li>"+modification.textualDescription+"</li>";});

		const message= 	"<div>"+
							"<p style='text-align: justify'>New coordination requirements have been added.</p>"+
							"<p style='text-align: justify'>The rest of participants have been informed about the following changes:</p>"+
							"<ul className='list-group'>"+changes+"</ul>"+
						"</div>";

		showMessage("Information about changes",message);
           
	}

	
	sendFragment(){

		if(this.localFragmentManager.checkBPMN()){
			switch(this.localFragmentManager.checkRequirementsModifications()){
				case 0: this.localFragmentManager.saveLocalFragment(); break;
				case -1: this.localFragmentManager.saveAsDirty(true, this.showModifications);break;
				case 1: this.localFragmentManager.saveAsDirty(false, this.showAddedElements);break;
			}	
		}
	}

	saveFragment(){
		this.localFragmentManager.saveOnlyLocalFragment();
	}

	render() {
  		return(
  			<button onClick={this.saveFragment} className="btn btn-primary">SAVE</button>
  		);
	}

 }