import React, { Component } from 'react';

export var showParticipantChanges = function(changes){
     window.participantChanges.showParticipantChanges(changes);
};

export default class ParticipantChanges extends React.Component {

  constructor(props) {
    super(props);

    this.state = {
      changes:[],
      stopEvent:true
    };

    window.participantChanges=this;
    this.showParticipantChanges=this.showParticipantChanges.bind(this);
    this.response=this.response.bind(this);
  }

  getChangeDescription(microservice, composition, changes){
      //var html='<div class="row" style="margin-top:50px;border:solid 1px white">';
      //html+='     <div class="offset-3 col-3" style="width:100%;text-align:center;color:white;font-size:1.5em;">Microservice: '+microservice+'</div>';
      //html+='     <div class="col-3" style="width:100%;text-align:center;color:white;font-size:1.5em;">Composition: '+composition+'</div>';
      //html+='</div>';
      var html='<div class="row" >';
      html+='     <div class="offset-3 col-6" style="border:solid 1px white; background-color:white;padding:40px 20px;">';
      html+='        <div class="row" >';
      html+='         <div class="col-6"><u><b>Microservice:</b></u>&nbsp;'+microservice+'<br/></div>';
      html+='         <div class="col-6"><u><b>Composition:</b></u>&nbsp;'+composition+'<br/></div>';
      html+='       </div>';
      html+='       <br/><u><b>Changes:</b></u>';
      html+='       <ul class="list-group list-group-flush">';
      var border='style="border:0px !important"';;
      changes.forEach(change=>{;html+='<li class="list-group-item" '+border+'">'+change.textualDescription+'</li>';border="";});
      html+='       </ul>';
      html+='     </div>';
      html+='</div>';
      return html;
  }

  showChangesWindow(){
    $("#participantMessage").css("display","inline");
  }

  showParticipantChanges(changes){
    $("#changes").empty();
    changes.forEach(change=>{$("#changes").append(this.getChangeDescription(change.microservice, change.composition, JSON.parse(change.changes)));});
    //$("#participantMessage").css("display","inline");

    this.setState({
        changes:changes
      });

    console.log(changes[0].accepted);

    if(changes[0].accepted===undefined){
      $("#showChangesDialog").modal();
    }
    else if(changes[0].accepted===2){
       this.showAdaptation();
       $("#confirmationDialog").modal();
    }else{
       this.showAdaptation();
    }
  }

  later(){
     $("#participantMessage").css("display","none");
  }

  responseDialog(acceptance){
    if(acceptance) $('#acceptDialog').modal('show');
    else $('#rejectDialog').modal('show');
  }

  response(acceptance){
    $("#sending-loader").css("display","inline");
    this.state.changes.forEach( change => {
      
        var datos=JSON.stringify({
          changeId:change.id,
          from:sessionStorage.getItem("microservicename"),
          to:change.microservice,
          composition:change.composition,
          response:acceptance
        });

        fetch(this.props.url+"/participant/change/reponse",{
              method:'POST',
              headers:{
                "Content-Type":"application/json",
              },
              body: datos
        })
        .then(function (response) {
            $("#acceptanceScreen").css("display","none");
            if(acceptance){
              $('#acceptDialog').modal('hide');
              $("#acceptedScreen").css("display","inline");
            }
            else{
              this.props.modeler.importXML(response, (err, warnings) => {
                $('#rejectDialog').modal('hide');
              });
            } 
            $("#sending-loader").css("display","none");
        })
        .catch(error => {
          $("#sending-loader").css("display","none");
          $("#acceptanceScreen").css("display","none");
          if(acceptance) $('#acceptDialog').modal('hide');
          else $('#rejectDialog').modal('hide');
          console.log(error);
          showMessage("Error","Some error occurs when accepting changes");
        })
    });
     
  }

  showAdaptation(){
    var dirtyXml=this.state.changes[0].dirtyXml;
    var microservice=this.state.changes[0].microservice;
    var changeId=this.state.changes[0].id;
    this.props.modeler.importXML(dirtyXml, (err, warnings) => {
        $("#participantMessage").css("display","none");
        if(this.state.changes[0].accepted===undefined)
           $("#acceptanceScreen").css("display","inline");
        else
           $("#acceptedScreen").css("display","inline");
        $("#bottom-area").css("display","none");
        this.disableInteraction();
         $("#showChangesDialog").modal('hide');
    });
    /*fetch(this.props.url+"/participant/change/adapted/fragment/"+microservice+"/"+changeId)
    .then(response => {
        return response.text()
      })
    .then(fragment => {
        $("#showChangesDialog").modal('hide');
        this.props.modeler.importXML(fragment, (err, warnings) => {
            $("#participantMessage").css("display","none");
            if(this.state.changes[0].accepted===undefined)
               $("#acceptanceScreen").css("display","inline");
            else
               $("#acceptedScreen").css("display","inline");
            $("#bottom-area").css("display","none");
            this.disableInteraction();
        });
    })
    .catch(error => {
      $("#participantMessage").css("display","none");
      console.log(error);
      showMessage("Error","Some error occurs when showing adaptation");
    })*/

  }

  disableInteraction(){
    var _this=this;
      this.props.modeler.get("eventBus").on('drag.start', 1500, function(event) {
        _this.stopEvent(event);
      });
      this.props.modeler.get("eventBus").on('element.dblclick', 1400, function(event) {
       _this.stopEvent(event);
      });
      this.props.modeler.get("eventBus").on('element.click', 1400, function(event) {
        _this.stopEvent(event);
      });
  }

  stopEvent(event){
      if(this.state.stopEvent){
        event.preventDefault();
        event.stopPropagation();
      } 
  }

  confirm(){
    var microservice=this.state.changes[0].microservice;
    var changeId=this.state.changes[0].id;
    $("#confirmationDialog").modal("hide");
    $("#sending-loader").css("display","inline");
    fetch(this.props.url+"/participant/change/"+microservice+"/"+changeId,{
              method:'PUT'}
        )
      .then(response => {
          return response.text()
        })
      .then(fragment => {
          console.log(fragment);
          this.props.modeler.importXML(fragment, (err, warnings) => {
            $("#acceptedScreen").css("display","none");
            $("#bottom-area").css("display","inline");
            $("#sending-loader").css("display","none");
            this.setState({stopEvent:false});
          });
      })
      .catch(error => {
        console.log(error);
        $("#sending-loader").css("display","none");
        showMessage("Error","Some error occurs when confirming changes");
      })
  }

  render(){
    var divStyle={
      display:"none",
      position:"absolute",
      top:"0px",
      left:"0px",
      width:"100%",
      height:"100%",
      backgroundColor: "rgba(125,125,125,0.9)",
      zIndex:1000,
    }

    var textStyle={
      margin:"100px auto 0px",
      width:"800px",
      fontSize:"2em",
      textAlign:"center",
      color:"white"
    }

    var button={
      textAlign:"center",
      width:"100%",
      backgroundColor:"white", 
      paddingBottom:"20px",
      paddingTop:"20px"
    };

    var redText={
      textAlign:"center",
      width:"100%",color:"red", 
      fontSize:"20px", 
      backgroundColor:"white", 
      paddingBottom:"20px",
      paddingTop:"20px"
    };

    var topBorder={
        borderTop:"solid 1px black"
    }

    var acceptance={
      display:"none"
    }

    return(
      <div>
    	  <div id="participantMessage" style={divStyle}>
    	    <div style={textStyle}>Change description</div>
          <div id="changes"></div>
          <div className="row" >
            <div className="offset-3 col-6" style={button}><button className="btn btn-info" onClick={this.later}>Hide</button></div>
          </div>
    	  </div>
        <div id="acceptanceScreen" style={acceptance}>
          <div className="row" style={topBorder}>
            <div className="offset-1 col-5" style={redText}>Showing fragment adapted to changes done by a participant&nbsp;</div>
            {/*<div className="col-2" style={redText}><button className="btn btn-secondary" onClick={this.showChangesWindow}>Show change description</button></div>*/}
            <div className="offset-1 col-2" style={button}><button className="btn btn-primary" onClick={this.responseDialog.bind(this, true)}>Accept</button></div>
            <div className="col-2" style={button}><button className="btn btn-danger" onClick={this.responseDialog.bind(this,false)}>Reject</button></div>
          </div>
        </div>
        <div id="acceptedScreen" style={acceptance}>
          <div className="row" style={topBorder}>
            <div className="offset-2 col-8" style={redText}>Showing accepted fragment adaptation. Waiting for confirmation</div>
          </div>
        </div>

        <div className="modal fade" tabIndex="-1" role="dialog" id="acceptDialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Your attention please</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <p>Are you sure yoy want to ACCEPT the proposed adaptation?</p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={this.response.bind(this, true)}>Yes</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">No</button>
              </div>
            </div>
          </div>
        </div>


        <div className="modal fade" tabIndex="-1" role="dialog" id="rejectDialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Your attention please</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <p>Are you sure yoy want to REJECT the proposed adaptation?</p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={this.response.bind(this, false)} data-dismiss="modal">Yes</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">No</button>
              </div>
            </div>
          </div>
        </div>

        <div className="modal fade" tabIndex="-1" role="dialog" id="showChangesDialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Your attention please</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <p>Some participant has changed its BPMN fragment and adapatation is required in yours. Do you want to evaluate it now?</p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={this.showAdaptation.bind(this)}>Yes</button>
                <button type="button" className="btn btn-secondary" data-dismiss="modal">No, later</button>
              </div>
            </div>
          </div>
        </div>

         <div className="modal fade" tabIndex="-1" role="dialog" id="confirmationDialog">
          <div className="modal-dialog" role="document">
            <div className="modal-content">
              <div className="modal-header">
                <h5 className="modal-title">Your attention please</h5>
                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
                  <span aria-hidden="true">&times;</span>
                </button>
              </div>
              <div className="modal-body">
                <p>The rest of affected particiapnts have accepted their adaptations. Yours will be confirmed in your BPMN fragment.</p>
              </div>
              <div className="modal-footer">
                <button type="button" className="btn btn-primary" onClick={this.confirm.bind(this)}>OK</button>
              </div>
            </div>
          </div>
        </div>

      </div>
    );
  }
}