import React, { Component } from 'react';
import './DirtyDialog.css';

export var showDirty = function(responses, participants, changeId, accepted){
     window.dirtyDialog.show(responses, participants, changeId, accepted);
};

export default class DirtyDialog extends Component {

	constructor(props) {
	    super(props);

	    this.state={
	        changeId:-1,
	        stopEvent:true
	    };

	    window.dirtyDialog=this;
	    this.show=this.show.bind(this);
	    this.disableInteraction=this.disableInteraction.bind(this);
	    this.confirm=this.confirm.bind(this);
	}

	show(responses, participants, changeId, accepted){

		$("#localChangeResponses").html(responses);
		$("#compositionParticipants").html(participants);

		if(accepted===undefined){
			$("#globalText").addClass("grey-text");
			$("#participantText").addClass("grey-text");
		}else{
			if(accepted>0){
				$("#globalAcceptance").html("yes");
				$("#globalText").addClass("blue-text");
			}
			/*if(accepted==2)
				$("#confirmDirtyDialog").modal("show");*/

			if(responses>=participants){
				$("#participantText").addClass("blue-text");
				$("#confirmDirtyDialog").modal("show");
			}else{
				$("#participantText").addClass("grey-text");
			}
		}

		this.disableInteraction();
		$("#dirty").css("display","inline");
        $("#bottom-area").css("display","none");

        this.setState({
	        changeId:changeId
	    });
	}

	disableInteraction(){
		var _this=this;
	    this.props.modeler.get("eventBus").on('drag.start', 1500, function(event) {
	      _this.stopEvent(event);
	    });
	    this.props.modeler.get("eventBus").on('element.dblclick', 1400, function(event) {
	     _this.stopEvent(event);
	    });
	    this.props.modeler.get("eventBus").on('element.click', 1400, function(event) {
	      _this.stopEvent(event);
	    });
	}

	stopEvent(event){
	  	if(this.state.stopEvent){
	  	  event.preventDefault();
    	  event.stopPropagation();
	  	} 
	}

	confirm(){
		$("#confirmDirtyDialog").modal("hide");
		$("#sending-loader").css("display","inline");
		fetch(this.props.url+"/local/change/"+this.state.changeId,{
              method:'PUT'}
        )
	    .then(response => {
	        return response.text()
	      })
	    .then(fragment => {
	        this.props.modeler.importXML(fragment, (err, warnings) => {
	           	$("#dirty").css("display","none");
       			$("#bottom-area").css("display","inline");
       			$("#sending-loader").css("display","none");
	            this.setState({stopEvent:false});
	        });
	    })
	    .catch(error => {
	      console.log(error);
	      $("#sending-loader").css("display","none");
	      showMessage("Error","Some error occurs when confirming changes");
	    })
	}
	
	render() {
  		return(
  		<div>
  			<div id="dirty">
			    <div className="row" >
			      <div className="col-5 dirty-text red-text" >This is a dirty copy of the BPMN fragment. Changes must be accepted by the Global Manager and the other composition participants.</div>
			      <div className="col-3 dirty-text" id="globalText">Accepted by the Global Manager? <span id="globalAcceptance">No</span></div>
			      <div className="col-3 dirty-text"	id="participantText">Accepted by <span id="localChangeResponses"></span> of <span id="compositionParticipants"></span> affected participants.</div>
			    </div>
			</div>

			<div className="modal fade" tabIndex="-1" role="dialog" id="confirmDirtyDialog">
	          <div className="modal-dialog" role="document">
	            <div className="modal-content">
	              <div className="modal-header">
	                <h5 className="modal-title">Your attention please</h5>
	                <button type="button" className="close" data-dismiss="modal" aria-label="Close">
	                  <span aria-hidden="true">&times;</span>
	                </button>
	              </div>
	              <div className="modal-body">
	                <p>Changes have been accepted by the Global Manager and the affected participant. They will be confirmed in the BPMN Fragment.</p>
	              </div>
	              <div className="modal-footer">
	                <button type="button" className="btn btn-primary" onClick={this.confirm} >OK</button>
	              </div>
	            </div>
	          </div>
	        </div>
		</div>
  		);
	}
}