export default class CoordinatorRequirementsAnalyzer{

	constructor(modeler) {
		this.modeler=modeler;
		this.coordChanges=[];

		this.getchanges=this.getChanges.bind(this);
		this.appendTextAnnotation=this.appendTextAnnotation.bind(this);
		this.createConnection=this.createConnection.bind(this);
		this.removeAction=this.removeAction.bind(this);
		this.extractANDundoCommandStack=this.extractANDundoCommandStack.bind(this);
		this.identifyChanges=this.identifyChanges.bind(this);
		this.includeDirtyMark=this.includeDirtyMark.bind(this);
	}
	
	getChanges(highlightModifications){
		this.coordChanges=this.modeler.get('commandStack')._stack.slice();
		if(highlightModifications) this.extractANDundoCommandStack();
		var changes=this.identifyChanges(highlightModifications);
		this.includeDirtyMark(this.modeler.get('canvas').getRootElement().businessObject.participants);
		return changes;
	}

	removeAction(numActions){
		var commandStack=this.modeler.get('commandStack');
		for(var i=0;i<numActions;i++){
			commandStack._stack.pop();
		}
		commandStack._stackIdx -= numActions;
	}

	extractANDundoCommandStack(){
		const commandStack = this.modeler.get('commandStack');
		var deleteAction=false;
		var numActions=0;

		var actionId=this.coordChanges[this.coordChanges.length-1].id;
		for(var i=this.coordChanges.length-1;i>=0;i--){
			var action=this.coordChanges[i];
			
			if(action.id!=actionId){
				if(deleteAction==true) commandStack.undo();
				else this.removeAction(numActions);

				numActions=0;
				deleteAction=false;
			} 

			if(action.command=="elements.delete") deleteAction=true;
			actionId=action.id;
			numActions++;
		};

		if(deleteAction==true) commandStack.undo();
		else this.removeAction(numActions);
	}


		
	identifyChanges(highlightModifications){
		const elementRegistry = this.modeler.get('elementRegistry');
		var coordinationReqType=["bpmn:IntermediateCatchEvent","bpmn:IntermediateThrowEvent"];
		var coordinationReqIDs=JSON.parse(sessionStorage.getItem("coordinationReq"));

		var changes=[];

		var deleteId="";	
		var replaceId="";
		var createId="";		
		var newShapeId="";
		console.log(this.coordChanges);
		this.coordChanges.forEach(action => {
			switch(action.command){
				case "elements.delete": 
											action.context.elements.forEach(element => {
												if(highlightModifications) this.drawInColor(element,"#FF0000");
												deleteId=action.id;
												
										
												if(coordinationReqIDs.includes(element.businessObject.id))
														changes.push({
																action:"deleteElement",
																elementType:element.businessObject.$type.substr(5),
																textualDescription:"",
																name:""
															});
											});
											break;
				case "element.updateLabel": 
											if(action.id==deleteId){
												//Add the event name to the description of elements.delete as this event loses this data
						
												if(changes[changes.length-1]!==undefined){
													changes[changes.length-1].name=action.context.oldLabel;
													changes[changes.length-1].textualDescription="Deleted a "+changes[changes.length-1].elementType+": "+action.context.oldLabel;
												}
											}else if(changes[changes.length-1] && changes[changes.length-1].action=="addElement" && changes[changes.length-1].name==null){
												changes[changes.length-1].name=action.context.newLabel;
												changes[changes.length-1].textualDescription="Added a new "+changes[changes.length-1].elementType+": "+action.context.newLabel;
											}else if(action.id!=replaceId && action.context.oldLabel!=null && action.context.newLabel!=null && action.context.oldLabel.length>0 && action.context.newLabel.length>0){

												if(highlightModifications) this.drawInColor(action.context.element, "#fd7e14");
												
												if(coordinationReqIDs.includes(action.context.element.businessObject.id))
														changes.push({
																action:"updateElement",
																udpate:"changeName",
																elementType:action.context.element.businessObject.$type.substr(5),
																textualDescription:"Change the name of "+action.context.element.businessObject.$type.substr(5)+". From "+action.context.oldLabel+" to "+action.context.newLabel,
																name:action.context.oldLabel,
																newName:action.context.newLabel
															});
												
					
											}
											
											break;
				case "shape.delete": 		
											if(action.id!=replaceId && highlightModifications){
												this.drawInColor(action.context.shape, "#FF0000");
											}
										 	break;
				case "connection.delete": 	
											if(highlightModifications) this.drawInColor(action.context.connection, "#FF0000");
									
											//We use the elementRegistry becuase the business object of the source shape has lost the name in a replace action
											var from=elementRegistry.get(action.context.source.id);
											var to=elementRegistry.get(action.context.target.id);

											if(action.id==replaceId){
												this.createConnection(from, to, action.context.connection.type, "#FF0000");
											}
										
											/*if(action.context.connection.type=="bpmn:MessageFlow")
													changes.push({
															action:"deleteConnection",
															elementType:action.context.connection.type,
															textualDescription:"Deleted a connection from "+from.businessObject.name+" to "+to.businessObject.name,
															from:from.businessObject.name,
															to:to.businessObject.name
														});*/
									 		break;
				
				case "shape.create": 		
											if(newShapeId.indexOf(action.context.shape.id)==-1){
												createId=action.id;
												newShapeId+=";"+action.context.shape.id;
												if(coordinationReqType.includes(action.context.shape.type)){
													if(action.context.shape.businessObject.name){  
															changes.push({
																	action:"createElement",
																	elementType:action.context.shape.type.substr(5),
																	textualDescription:"Added a new "+action.context.shape.type.substr(5)+": "+action.context.shape.businessObject.name,
																	name:action.context.shape.businessObject.name
																});
													}else{
															changes.push({
																	action:"createElement",
																	elementType:action.context.shape.type.substr(5),
																	textualDescription:"Added a new "+action.context.shape.type.substr(5)+" without name"
																});
													}
												}
											}
											if(highlightModifications) this.drawInColor(action.context.shape, "#00FF00");
										  	break;
				case "shape.replace": 
											if(createId==action.id || newShapeId.indexOf(action.context.oldShape.id)==-1){
												if(createId==action.id) changes.pop();
											  	replaceId=action.id;
											  	if(highlightModifications) this.drawInColor(action.context.newShape, "#fd7e14");
											  
											  	var oldType=action.context.oldShape.type;
											  	var newType=action.context.newShape.type.substr(5);
												if(coordinationReqType.includes(oldType) || coordinationReqType.includes(newType)){
												  	oldType=oldType.substr(5);
												  	newType=newType.substr(5);

												  	if(oldType.indexOf("Event")>=0){
												  		var eventType=action.context.oldShape.businessObject.eventDefinitions[0].$type;
												  		oldType+="("+eventType.substr(5, eventType.length-20)+")"; // 20 = 5 + 15->EventDefinition
												  	} 
												  	this.appendTextAnnotation(action.context.newShape.id, action.context.newShape.x-50,action.context.newShape.y-50,"Before it was an "+oldType, "#fd7e14");

												  	if(newType.indexOf("Event")>=0){
												  		var eventType=action.context.newShape.businessObject.eventDefinitions[0].$type;
												  		newType+="("+eventType.substr(5, eventType.length-20)+")"; // 20 = 5 + 15->EventDefinition
												  	}        

											  		changes.push({
															action:"updateElement",
															update:"changeType",
															elementType:oldType,
															textualDescription:"Changed the type of "+action.context.newShape.businessObject.name+". From "+oldType+" to "+newType,
															name:action.context.newShape.businessObject.name,
															newType:newType
														});
											  	}
											}
											break;
				case "connection.create": 	
									 		if(highlightModifications) this.drawInColor(action.context.connection, "#00FF00");

									 		/*if(action.context.connection.type=="bpmn:MessageFlow"){
										 		var fromName=action.context.connection.source.businessObject.name;
										 		var from=fromName?fromName:action.context.source.type.substr(5)+" without name";
										 		var toName=action.context.connection.target.businessObject.name;
										 		var to=toName?toName:action.context.connection.target.type.substr(5)+" without name";


										 		changes.push({
																action:"addConnection",
																elementType:action.context.connection.type,
																textualDescription:"Add connection between "+from+" and "+to,
																from:from,
																to:to
															});
										 	}*/
									 		break;
			}

		});

		
		return changes;
	}

	drawInColor(element, color){
		this.modeler.get('modeling').setColor(element, {
	      	stroke: color
	    });
	}

	applyCoordinationChanges(){
		const commandStack = this.modeler.get('commandStack');

		while(this.coordChanges.length>0) {
			var action = this.state.coordChanges.shift();
			if(action.command!="connection.delete" && action.command!="shape.delete"){
			  	commandStack.execute(action.command, action.context);
			} 
		}
	}


	appendTextAnnotation(id,x,y, text, color){ 
		var elementFactory = this.modeler.get('elementFactory'),
		  elementRegistry = this.modeler.get('elementRegistry'),
		  moddle = this.modeler.get('moddle'),
		  modeling = this.modeler.get('modeling');

		// create the new text annotation 
		var textAnnotation = elementFactory.createShape({
		  type: 'bpmn:TextAnnotation',
		  businessObject: moddle.create('bpmn:TextAnnotation', {
		    text: text
		  })
		});

		//append the new text annotation to our shape
		modeling.appendShape(elementRegistry.get(id), textAnnotation, {
		   x: x,
		   y: y
		});

		if(color) this.drawInColor(textAnnotation, color);
	}


	createConnection(from, to, type, color){
		var moddle = this.modeler.get('moddle'),
		  modeling = this.modeler.get('modeling');

		var connection=modeling.createConnection(
		  from,
		  to,
		  {
		    type: type,
		    businessObject: moddle.create(type)
		  },
		  from.parent
		);

		if(color) this.drawInColor(connection, color);
	}

	includeDirtyMark(participants){
		const modeling = this.modeler.get('modeling');
		const elementRegistry = this.modeler.get('elementRegistry');
		const moddle= this.modeler.get('moddle');

		participants.forEach(participant => {
			if(participant.name!="EVENT BUS"){
				/*modeling.updateProperties(elementRegistry.get(participant.id), {
			      'custom:dirty': true
			    });*/
			    var camundaNs = 'http://camunda.org/schema/1.0/bpmn';

		        var field=moddle.createAny('camunda:field',camundaNs, {name:"dirty", stringValue:"true"});
 				var extensionElements = moddle.create('bpmn:ExtensionElements');
		        extensionElements.get('values').push(field);
		       
		        modeling.updateProperties(elementRegistry.get(participant.id), {'extensionElements':extensionElements});
			}
		});
       
	}

}
